	module.exports = {"data":{"markdownRemark":{"html":"<h2>Tautologies</h2>\n<h3>Definition</h3>\n<p>In logic, a tautology is a statement that is true in every possible case. The opposite of a tautology is a contradiction, a statement being false in every possible cases.</p>\n<h3>Example</h3>\n<table>\n  <tr>\n    <th>p</th>\n    <th>q</th> \n    <th>p OR q</th>\n    <th>p → p OR q</th>\n  </tr>\n  <tr>\n    <td>T</td>\n    <td>T</td>\n    <td>T</tq>\n    <td>T</td>\n  </tr>\n  <tr>\n    <td>T</td>\n    <td>F</td>\n    <td>T</td>\n    <td>T</td>\n  </tr>\n  <tr>\n    <td>F</td>\n    <td>T</td>\n    <td>T</td>\n    <td>T</td>\n  </tr>\n  <tr>\n    <td>F</td>\n    <td>F</td>\n    <td>F</td>\n    <td>T</td>\n  </tr>\n</table>\n<p>As we can see in the truth table, the statement “p → p OR q” is always true (see last column). </p>\n<h4>Note</h4>\n<p>The arrow simply means “implies”. p implies p OR q, it can also mean <i>if…then…</i>  </p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://en.wikipedia.org/wiki/Tautology_(logic)\">Wikipedia Tautology (Logic)</a>\n<a href=\"https://www.youtube.com/watch?v=O0KbymjE7xU\">Youtube Truth Tables</a>\n<a href=\"https://en.wikipedia.org/wiki/List_of_logic_symbols\">Wikipedia Logic Symbols</a></p>","fields":{"slug":"/logic/tautologies/"},"frontmatter":{"title":"Tautologies"}}},"pathContext":{"slug":"/logic/tautologies/"}}

